import React from "react";
import "./styles.css";
import Child from "./Child";
class App extends React.Component {
  state = {
    v: ""
  };
  f = (e) => {
    this.setState({ v: e });
  };
  render() {
    return (
      <div>
        <Child v={(e) => this.f(e)} />
        <h1>{"State: " + this.state.v}</h1>
      </div>
    );
  }
}
export default App;
